'use client'

import { useEffect, useState } from 'react'
import { DashboardHeader } from '@/components/dashboard-header'
import { SpendingOverview } from '@/components/spending-overview'
import { SpendingChart } from '@/components/spending-chart'
import { CategoryBreakdown } from '@/components/category-breakdown'
import { TransactionsList } from '@/components/transactions-list'
import { TrendChart } from '@/components/trend-chart'

interface DashboardData {
  totalSpent: number
  transactionCost: number
  categoriesCount: number
  transactionsCount: number
}

export default function Dashboard() {
  const [data, setData] = useState<DashboardData>({
    totalSpent: 0,
    transactionCost: 0,
    categoriesCount: 0,
    transactionsCount: 0,
  })
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    // Simulated API call with sample data
    const fetchData = async () => {
      try {
        // Replace with actual API calls to your backend
        setData({
          totalSpent: 156389.5,
          transactionCost: 386.75,
          categoriesCount: 12,
          transactionsCount: 248,
        })
      } catch (error) {
        console.error('Failed to fetch dashboard data:', error)
      } finally {
        setIsLoading(false)
      }
    }

    fetchData()
  }, [])

  if (isLoading) {
    return (
      <div className="min-h-screen bg-background flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-primary mx-auto mb-4"></div>
          <p className="text-foreground/60">Loading dashboard...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-background">
      <DashboardHeader />
        <DateRangePicker/>
      <main className="container mx-auto px-4 py-8 space-y-8">
        {/* Overview Cards */}
        <SpendingOverview data={data} />

        {/* Charts Section */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          <div className="lg:col-span-2">
            <SpendingChart />
          </div>
          <div className="lg:col-span-1">
            <CategoryBreakdown />
          </div>
        </div>

        {/* Trend and Transactions */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          <TrendChart />
          <TransactionsList />
        </div>
      </main>
    </div>
  )
}
